import {Component, OnInit} from "@angular/core";
import {ManagedObject} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";
import {SupervisorServiceContext} from "../model/supervisor-service-context";
import {SupervisorService} from "@generated/supervisor-service";
import {SupervisorNamespaceService} from "@generated/supervisor-namespace-service";

@Component({
   templateUrl: "edit-supervisor-service.html",
   styleUrls: ["edit-supervisor-service.scss"],
   selector: "vsan-edit-supervisor-service"
})
export class EditSupervisorServiceComponent implements OnInit {

   alertMessages: string[] = null;
   loading: boolean = false;
   clusterRef: ManagedObject;
   clusterName: string;
   supervisorService: SupervisorService;
   isEulaAccepted: boolean;
   version: string;

   constructor(private supervisorNamespaceService: SupervisorNamespaceService) {
   }

   ngOnInit(): void {
      const context: SupervisorServiceContext = VsanUiUtils.getModalContext() as SupervisorServiceContext;
      this.clusterRef = context.clusterRef;
      this.clusterName = context.clusterName;
      this.supervisorService = context.supervisorService;
      if (this.supervisorService.availableVersions.indexOf(this.supervisorService.desiredVersion) !== -1) {
         // If the desired version is present in the available versions list, preselect it
         this.version = this.supervisorService.desiredVersion;
      } else {
         // Otherwise - preselect the latest one from the list
         this.version = this.supervisorService.availableVersions[this.supervisorService.availableVersions.length - 1];
      }
   }

   apply = () => {
      this.loading = true;
      const clone: SupervisorService = VsanUiUtils.deepClone(this.supervisorService);
      clone.isEnabled = true;
      clone.version = this.version;
      this.supervisorNamespaceService
            .updateSupervisorService(this.clusterRef, clone)
            .then(VsanUiUtils.closeModalDialog)
            .catch(this.handleError);
   };

   handleError = (error: any) => {
      this.loading = false;
      this.alertMessages = [error];
   };
}
