import {Component, ElementRef, EventEmitter, HostBinding, Output} from "@angular/core";

/**
 * Helper component used by flex-expandable.directive.ts. Displays an anchor for expand / collapse functionality.
 */
@Component({
   templateUrl: "flex-expandable-anchor.html",
   styleUrls: ["flex-expandable-anchor.scss"],
   selector: "vsan-flex-expandable-anchor",
})
export class FlexExpandableAnchorComponent {

   private _isExpanded: boolean = false;
   set isExpanded( value: boolean) {
      this._isExpanded = value;
      this.isCollapsedContainer = !value;
      this.isExpandedContainer = value;
   }

   get isExpanded(): boolean {
      return this._isExpanded;
   }

   @HostBinding("class.collapsed-state")
   isCollapsedContainer: boolean = !this.isExpanded;

   @HostBinding("class.expanded-state")
   isExpandedContainer: boolean = this.isExpanded;

   @Output()
   expandState: EventEmitter<boolean> = new EventEmitter();

   constructor(public element: ElementRef) {}

   expand(isExpanded: boolean): void {
      this.expandState.emit(isExpanded);
      this.isExpanded = isExpanded;
   }

}