import {Component, OnInit} from "@angular/core";
import {PermissionService} from "@generated/permission-service";
import {Privileges} from "@generated/privileges";
import {SupervisorService} from "@generated/supervisor-service";
import {SupervisorServicesService} from "@generated/supervisor-services-service";
import {VsanCapabilityProvider} from "@generated/vsan-capability-provider";
import {ClusterComputeResource} from "@service/client/dataservice/data-service-property";
import {DataServiceClient} from "@service/client/dataservice/data-service.service";
import {ErrorMessage} from "@service/flow/error-message.decorator";
import {Loader} from "@service/flow/loader.decorator";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {RefreshHandler} from "@service/global-refresh.service";
import {ManagedObject} from "@service/managed-object";
import {Icon} from "@util/icon";
import {ModalBuilder} from "@util/modal-builder";
import {VsanUiUtils} from "@util/vsan-util";
import {SupervisorServiceContext} from "../model/supervisor-service-context";

@Component({
   templateUrl: "supervisor-services.html",
   styleUrls: ["supervisor-services.scss"],
   selector: "vsan-all-supervisor-services",
})
export class SupervisorServicesComponent implements OnInit {
   Icon: typeof Icon = Icon;

   private static readonly EDIT_DIALOG_WIDTH: number = 670;
   private static readonly EDIT_DIALOG_HEIGHT: number = 505;
   private readonly PRIVILEGES: string[] = [Privileges.NAMESPACES_CONFIGURE(), Privileges.NAMESPACES_MANAGE()];

   @LoadingIndicator()
   loading: boolean;
   clusterRef: ManagedObject;
   clusterName: string;
   selectedService: SupervisorService;
   services: SupervisorService[];
   @ErrorMessage()
   error: string;
   hasEditPrivileges: boolean;

   private isAirGapSupported: boolean = false;

   constructor(private supervisorServicesService: SupervisorServicesService,
         private permissionService: PermissionService,
         private capabilityService: VsanCapabilityProvider,
         private dataService: DataServiceClient) {
   }

   ngOnInit(): void {
      this.clusterRef = ManagedObject.contextObject;

      this.loadClusterProps();
      this.loadCapabilities();
      this.loadPermissions();
      this.loadServices();
   }

   @Loader()
   private async loadClusterProps(): Promise<void> {
      this.clusterName = await this.dataService.getProperty(ClusterComputeResource.name, this.clusterRef);
   }

   @Loader()
   private async loadCapabilities(): Promise<void> {
      this.isAirGapSupported = await this.capabilityService.isPersistenceServiceAirGapSupportedOnVc(this.clusterRef);
   }

   @Loader()
   private loadPermissions(): Promise<boolean> {
      return this.permissionService.hasPermissions(this.clusterRef, this.PRIVILEGES)
            .then((result: boolean) => this.hasEditPrivileges = result)
            .catch(() => this.hasEditPrivileges = true);
   }

   @RefreshHandler
   @Loader({srMessage: "vsan.config.ss.supervisorServices"})
   private async loadServices(): Promise<void> {
      this.services = await this.supervisorServicesService.list(this.clusterRef);
   }

   getStatusLabel(isEnabled: boolean): string {
      const key: string = isEnabled ? "vsan.config.ss.supervisorServices.header.status.enabled" :
            "vsan.config.ss.supervisorServices.header.status.disabled";
      return VsanUiUtils.getString(key);
   }

   enable(): void {
      const context: SupervisorServiceContext = {
         supervisorService: this.selectedService,
         clusterRef: this.clusterRef,
         clusterName: this.clusterName,
      };
      if (this.isAirGapSupported) {
         new ModalBuilder("supervisorServices/editWizard")
               .setDimensions(ModalBuilder.WIZARD_XL)
               .open(context)
               .then(() => this.loadServices());
      } else {
         new ModalBuilder("supervisorServices/editDialog")
               .setSize(SupervisorServicesComponent.EDIT_DIALOG_WIDTH, SupervisorServicesComponent.EDIT_DIALOG_HEIGHT)
               .open(context)
               .then(() => this.loadServices());
      }
   }

   disable(): void {
      const context: SupervisorServiceContext = {
         supervisorService: this.selectedService,
         clusterRef: this.clusterRef,
         clusterName: this.clusterName,
      };
      new ModalBuilder("supervisorServices/disable")
            .setSize(600, 260)
            .open(context)
            .then(() => this.loadServices());
   }

   isEnableButtonDisabled(): boolean {
      return this.selectedService == null
            || this.selectedService.availableVersions == null
            || this.selectedService.availableVersions.length === 0
            || !this.hasEditPrivileges;
   }

   getEnableButtonTitle(): string {
      if (!this.hasEditPrivileges) {
         return VsanUiUtils.getString("vsan.common.nopermissions");
      }

      if (this.selectedService.availableVersions != null && this.selectedService.availableVersions.length > 0) {
         return VsanUiUtils.getString("vsan.config.ss.supervisorServices.action.enable.title");
      }

      return VsanUiUtils.getString("vsan.config.ss.supervisorServices.action.enable.title.noVersions");
   }
}
