import {Component, OnInit} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {SupervisorService} from "@generated/supervisor-service";
import {InputProperty, WizardPage} from "@component/wizard/flow-metadata";
import {OnValidate, PageControls} from "@component/wizard/flow-wizard.component";
import {AdvancedSetting} from "@generated/advanced-setting";
import {Logger} from "@util/logger";
import {NavigationService} from "@navigation/navigation.service";

@WizardPage(VsanUiUtils.getString("vsan.config.ss.edit.general.title"))
@Component({
   templateUrl: "settings-page.html",
   styleUrls: ["settings-page.scss"],
})
export class SettingsPageComponent implements OnInit, OnValidate {

   readonly KB_PSP_SERVICE_SETTINGS: string = "https://docs.vmware.com/";
   readonly SETTING_KEY_MAX_LENGTH: number = 1048;
   readonly SETTING_VALUE_MAX_LENGTH: number = 1048;
   private static readonly MAX_SETTINGS: number = 12;

   @InputProperty()
   supervisorService: SupervisorService;

   @InputProperty()
   pageControls: PageControls;

   constructor(private navigationService: NavigationService) {
   }

   ngOnInit() {
      if (!this.supervisorService.version) {
         this.supervisorService.version = this.supervisorService.desiredVersion;
      }
   }

   validate(): string[] | Promise<string[]> {
      const errorKeys: string[] = [];
      if (this.supervisorService.username && VsanUiUtils.isEmpty(this.supervisorService.repo)) {
         errorKeys.push("vsan.persistence.enable.noRepo");
      }
      if (this.supervisorService.password && VsanUiUtils.isEmpty(this.supervisorService.username)) {
         errorKeys.push("vsan.persistence.enable.noUsername");
      }
      const incompleteSettings: boolean = this.supervisorService.advancedSettings
            .some(setting => VsanUiUtils.isEmpty(setting.key) || VsanUiUtils.isEmpty(setting.value));
      if (incompleteSettings) {
         errorKeys.push("vsan.persistence.enable.incompleteSettings");
      }

      return errorKeys.length
            ? errorKeys.map(key => VsanUiUtils.getString(key))
            : null;
   }

   invalidate() {
      this.pageControls.rebuildToc();
   }

   get isAddAllowed(): boolean {
      return this.supervisorService.advancedSettings.length < SettingsPageComponent.MAX_SETTINGS;
   }

   get addButtonTooltip(): string {
      return this.isAddAllowed
            ? VsanUiUtils.getString("vsan.persistence.enable.add.allowed")
            : VsanUiUtils.getString("vsan.persistence.enable.add.disabled");
   }

   addSetting() {
      if (this.isAddAllowed) {
         this.supervisorService.advancedSettings.push(AdvancedSetting.Factory.create("", ""));
         this.invalidate();
      } else {
         Logger.debug("Adding more advanced settings is not allowed.");
      }
   }

   removeSetting(index: number) {
      this.supervisorService.advancedSettings.splice(index, 1);
      this.invalidate();
   }
}
