import {Component, OnInit} from "@angular/core";
import {PageSet} from "@component/wizard/flow-wizard.component";
import {SupervisorNamespaceService} from "@generated/supervisor-namespace-service";
import {SupervisorServiceContext} from "../model/supervisor-service-context";
import {VsanUiUtils} from "@util/vsan-util";
import {EulaPageComponent} from "./eula-page.component";
import {SupervisorServiceWizardContext} from "../model/supervisor-service-wizard-context";
import {SettingsPageComponent} from "./settings-page.component";
import {SupervisorService} from "@generated/supervisor-service";

@Component({
   selector: "vsan-edit-supervisor-service-wizard",
   template: `
      <vsan-flow-wizard
            [title]="'vsan.config.ss.supervisorServices.action.enable.title' | localized"
            [pageSets]="pages"
            [contextProperties]="context"
            [startingTaskProperty]="'updateSupervisorService'"
            [services]="services">
      </vsan-flow-wizard>`,
})
export class EditSupervisorServiceWizardComponent implements OnInit {

   context: SupervisorServiceWizardContext;
   readonly services: any[] = [SupervisorNamespaceService];

   readonly pages: PageSet[] = [
      new PageSet([SettingsPageComponent]),
      new PageSet([EulaPageComponent]),
   ];

   ngOnInit(): void {
      const navigationContext: SupervisorServiceContext = VsanUiUtils.getModalContext() as SupervisorServiceContext;
      if (!navigationContext.supervisorService.advancedSettings) {
         navigationContext.supervisorService.advancedSettings = [];
      }

      const clone: SupervisorService = VsanUiUtils.deepClone(navigationContext.supervisorService);
      clone.isEnabled = true;

      this.context = {
         clusterRef: navigationContext.clusterRef,
         supervisorService: clone,
      };
   }
}
