import {NgModule} from "@angular/core";
import {FormsModule} from "@angular/forms";
import {ClarityModule} from "@clr/angular";
import {CommonModule} from "@angular/common";
import {RouterModule, Routes} from "@angular/router";
import {VsanCommonModule} from "../common/vsan-common.module";
import {SupervisorServicesComponent} from "./list/supervisor-services.component";
import {EditSupervisorServiceComponent} from "./edit/edit-supervisor-service.component";
import {DisableSupervisorServiceComponent} from "./disable/disable-supervisor-service.component";
import {EditSupervisorServiceWizardComponent} from "./edit/edit-supervisor-service-wizard.component";
import {EulaPageComponent} from "./edit/eula-page.component";
import {FlowWizardModule} from "@component/wizard/flow-wizard.module";
import {SettingsPageComponent} from "./edit/settings-page.component";

const supervisorServicesRoutes: Routes = [
   {
      path: "",
      component: SupervisorServicesComponent
   },
   {
      path: "editDialog",
      component: EditSupervisorServiceComponent
   },
   {
      path: "editWizard",
      component: EditSupervisorServiceWizardComponent
   },
   {
      path: "disable",
      component: DisableSupervisorServiceComponent
   },
];

@NgModule({
   imports: [
      FormsModule,
      CommonModule,
      ClarityModule,
      FlowWizardModule,
      VsanCommonModule,
      RouterModule.forChild(supervisorServicesRoutes)
   ],
   declarations: [
      SupervisorServicesComponent,
      EditSupervisorServiceComponent,
      EditSupervisorServiceWizardComponent,
      SettingsPageComponent,
      EulaPageComponent,
      DisableSupervisorServiceComponent,
   ],
   entryComponents: [
      EditSupervisorServiceComponent,
      SettingsPageComponent,
      EulaPageComponent,
   ]
})
export class SupervisorServicesModule {
}
