import {
   AfterContentInit,
   ComponentFactory,
   ComponentFactoryResolver,
   ComponentRef,
   Directive,
   ElementRef,
   Renderer2,
   ViewContainerRef
} from "@angular/core";
import {FlexExpandableAnchorComponent} from "./flex-expandable-anchor.component";

/**
 * When applied on container in flex parent - expands it to fill the parent. If there is content which stays hidden
 * because lack of space displays "show more..." link. Clicking on the link the container expands in order to show all
 * content. "show less..." link also appears with possibility to collapse the container back to initial state.
 *
 */
@Directive({
   selector: "[vsan-flex-expandable]",
   host: {
      "[style.overflow]": "'hidden'",
      "[style.flex]": "'1 1 auto'",
      "[style.position]": "'relative'",
   }
})
export class FlexExpandableDirective implements AfterContentInit {

   private isOverflow: boolean;

   private initialFlexValue: string;

   constructor(private viewContainerRef: ViewContainerRef,
         private el:ElementRef<HTMLElement>,
         private resolver: ComponentFactoryResolver,
         private renderer: Renderer2) {
   }

   ngAfterContentInit(): void {
      setTimeout(() => {
         // cache the initial flex property value in order to restore them on collapsing
         this.initialFlexValue = this.el.nativeElement.style.flex;
         // calculate if there is hidden content
         this.isOverflow = this.el.nativeElement.scrollHeight > this.el.nativeElement.clientHeight;
         if (this.isOverflow) {
            this.addAnchor();
         }
      });
   }

   expand = (doExpand: boolean) => {
      this.el.nativeElement.style.flex = doExpand ? '0 0 auto' : this.initialFlexValue;
   }

   private addAnchor(): void {
      const factory: ComponentFactory<FlexExpandableAnchorComponent> =
            this.resolver.resolveComponentFactory(FlexExpandableAnchorComponent);
      const anchorRef: ComponentRef<FlexExpandableAnchorComponent> = this.viewContainerRef.createComponent(factory);
      const anchor: FlexExpandableAnchorComponent = anchorRef.instance;
      anchor.expandState.subscribe(isExpanded => this.expand(isExpanded));
      this.renderer.appendChild(this.el.nativeElement, anchor.element.nativeElement);
   }
}