import {Component} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {SupervisorService} from "@generated/supervisor-service";
import {OnValidate} from "@component/wizard/flow-wizard.component";
import {InputProperty, WizardPage} from "@component/wizard/flow-metadata";

@WizardPage(VsanUiUtils.getString("vsan.config.ss.edit.eula.title"))
@Component({
   templateUrl: "eula-page.html",
   styleUrls: ["eula-page.scss"]
})
export class EulaPageComponent implements OnValidate {

   @InputProperty()
   supervisorService: SupervisorService;

   isEulaAccepted: boolean;

   validate(): string[] | Promise<string[]> {
      return this.isEulaAccepted
            ? null
            : [VsanUiUtils.getString("vsan.persistence.enable.acceptEula.error")];
   }
}
